
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import VChart from 'vux/src/components/v-chart/v-chart.vue'
import VTooltip from 'vux/src/components/v-chart/v-tooltip.vue'
import VLine from 'vux/src/components/v-chart/v-line.vue'
import VScale from 'vux/src/components/v-chart/v-scale.vue'
import VGuide from 'vux/src/components/v-chart/v-guide.vue'
import Panel from 'vux/src/components/panel/index.vue'


const data = []


function getRecord (offset) {
offset = offset || 0
return {
time: new Date().getTime() + offset * 1000,
value: Math.random() + 10
}
}

data.push(getRecord(-2))
data.push(getRecord(-1))
data.push(getRecord())

export default {
name: 'Home2',
components: {
Group,
Cell,
Tabbar,
TabbarItem,
VChart,
VTooltip,
VLine,
VScale,
VGuide,
Panel
},
mounted () {
this.timer = setInterval(() => {
data.push(getRecord())
}, 2000)
},
beforeDestroy () {
clearInterval(this.timer)
},
data () {
return {




msg: 'Hello World!',
type: '4',
list: [{
src: 'http://somedomain.somdomain/x.jpg',
fallbackSrc: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
title: '例行检修',
desc: '2018年第3季度的例行检修。',
url: '/component/cell'
}, {
src: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
title: '试运行',
desc: '开始设备试运行。',
url: {
path: '/component/radio',
replace: false
},
meta: {
source: '来源信息',
date: '时间',
other: '其他信息'
}
}],
data
}
},
methods: {
onItemClick (index) {
console.log('On item click ：', index)
},
onIndexChange (newIndex, oldIndex) {
console.log(newIndex, oldIndex)
}
}
}



